import React from "react"
import { Section, Card, Heading, Button, GifPlayer } from "components"
import { Row, Col } from "antd"
import { Link, withRouter } from "react-router-dom"
import { Trans } from "@lingui/macro"
import { country } from "App"

function AccountActive({ history, location }) {
	const params = new URLSearchParams(location.search)
	const success = params.get("success")

	return (
		<Section>
			<Row type="flex" justify="center" align="middle">
				<Col lg={12}>
					<Section centered textAlign="center">
						<Link to={`/${country}/`}>
							<img alt="" src="https://app.cudy.co/img/logo_live.png" width="100" />
						</Link>
					</Section>
					<Card autoHeight noHover>
						{success ? (
							<Section centered textAlign="center">
								<GifPlayer
									src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json"
									width={250}
									height={250}
									class="mb2em"
								/>
								<Heading
									content={<Trans>Well done!</Trans>}
									subheader={<Trans>Your account is active. Now start logging in to browse best tutors around us.</Trans>}
									marginBottom="3em"
								/>
								<Link to={`/${country}/login`}>
									<Button type="ghost" icon="user">
										&nbsp; <Trans>Go to login</Trans>
									</Button>
								</Link>
							</Section>
						) : (
							<Section centered textAlign="center">
								<Heading
									content="Oops!"
									subheader={<Trans>Something wrong happened. you might want to go back to the Home page</Trans>}
									marginBottom="3em"
								/>
								<Link to={`/${country}/`}>
									<Button type="ghost" icon="home">
										&nbsp; <Trans>Go to Home</Trans>
									</Button>
								</Link>
							</Section>
						)}
					</Card>
				</Col>
			</Row>
		</Section>
	)
}

export default withRouter(AccountActive)
